aside#sidebar{
  nav{
    ul{
      margin: 0;
      padding: 0;
      list-style: none;

      li{
        line-height: 27px;
        height: 27px;

        a{
          text-decoration: none;
          color: #7f8c8d;
          font-size: 15px;

          &:hover, &.active{
            border-bottom: 2px solid $detail-color;
          }

          &.active{
            font-weight: bold;
          }
        }
      }
    }
  }
}