.h1, .h2, h1, h2 {
  display: block;
  font-weight: 600; }

.h1, h1 {
  margin-bottom: 30px;
  font-size: 30px; }

.h2, h2 {
  margin-top: 4px;
  margin-bottom: 18px;
  font-size: 22px; }

.fix {
  position: fixed !important; }

.article-logo {
  float: left;
  margin-bottom: 10px;
  margin-right: 20px; }

* {
  color: #333; }

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

main {
  display: flex;
  position: relative;
  flex-flow: row;
  flex: 1;
  padding-top: 30px; }

aside#sidebar {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 60px;
  padding: 30px 0;
  width: 260px;
  margin-right: 20px;
  overflow-x: hidden;
  overflow-y: auto; }

aside#content {
  padding-right: 60px;
  padding-left: 330px;
  display: flex;
  flex-direction: column; }

@media all and (max-width: 640px) {
  main {
    flex-direction: column; }
  aside#content, aside#sidebar {
    order: 0; }
  aside#sidebar, header, footer {
    min-height: 50px;
    max-height: 50px; } }

header {
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.25);
  padding: 25px 60px;
  line-height: 44px;
  display: flex;
  flex-flow: row; }
  header h1 {
    margin: 0; }
  header #left, header #right {
    display: flex;
    height: 44px; }
  header #left {
    flex: 1 20%;
    order: 1; }
  header #right {
    flex: 1 80%;
    order: 2;
    justify-content: flex-end; }
    header #right nav ul {
      margin: 0;
      padding: 0;
      list-style: none;
      display: flex;
      flex-direction: row; }
      header #right nav ul li {
        margin: 0 12px; }
        header #right nav ul li a {
          text-decoration: none;
          font-size: 15px;
          padding-bottom: 3px; }
          header #right nav ul li a:hover, header #right nav ul li a.active {
            border-bottom: 2px solid #ff8f34; }
        header #right nav ul li input[type="text"] {
          height: 30px;
          line-height: 30px;
          box-sizing: border-box;
          font-size: 13px;
          padding: 0 15px 0 30px;
          border: 1px solid #e3e3e3;
          outline: none;
          border-radius: 15px;
          margin-right: 10px;
          transition: border-color 0.2s ease;
          background: #fff url("../images/common/search.png") 8px 5px no-repeat;
          background-size: 20px; }

aside#sidebar nav ul {
  margin: 0;
  padding: 0;
  list-style: none; }
  aside#sidebar nav ul li {
    line-height: 27px;
    height: 27px; }
    aside#sidebar nav ul li a {
      text-decoration: none;
      color: #7f8c8d;
      font-size: 15px; }
      aside#sidebar nav ul li a:hover, aside#sidebar nav ul li a.active {
        border-bottom: 2px solid #ff8f34; }
      aside#sidebar nav ul li a.active {
        font-weight: bold; }
